const fetchData = async search => {
    try {
        if (search.loading) return
        if (search.term === '') return search
        if (search.type === 'text') {
            const response = await fetch(
                `/api/v1/movies/search?text=${search.term}&page=${search.page}`
            )
            return await response.json()
        } else {
            const queryList = search.term.split(',')
            const queryArray = queryList.map(elem => `${search.type}[]=${elem.trim()}`)
            const queryString = queryArray.join('&')
            const response = await fetch(`/api/v1/movies/search?${queryString}&page=${search.page}`)
            return await response.json()
        }
    } catch (error) {
        console.log(error)
        throw new Error(error)
    }
}

export default fetchData
