import { useState } from 'react'

const ReviewForm = ({ initialValue, submitHandler, mode, switchEditMode, commentId }) => {
    const [text, setText] = useState(initialValue)

    const handleSubmit = e => {
        e.preventDefault()
        if (!text) return
        submitHandler(text, commentId)
        setText('')
    }

    return (
        <form onSubmit={handleSubmit}>
            <div className='mb-3'>
                <textarea
                    value={text}
                    onChange={e => setText(e.target.value)}
                    className='form-control'
                    cols='30'
                    rows='5'
                    placeholder='What do you think of this movie?'
                    required
                ></textarea>
            </div>
            {mode === 'new' ? (
                <button className='btn w-25 btn-outline-success' type='submit'>
                    Add Review
                </button>
            ) : (
                <div className='d-flex justify-content-between'>
                    <button className='btn btn-link link-success m-0 p-0' type='submit'>
                        Submit
                    </button>
                    <button
                        className='btn btn-link link-danger m-0 p-0'
                        type='button'
                        onClick={() => switchEditMode()}
                    >
                        Cancel
                    </button>
                </div>
            )}
        </form>
    )
}

export default ReviewForm
