import SEARCH_ACTIONS from '../actions/searchActions'

const searchReducer = (search, action) => {
    switch (action.type) {
        case SEARCH_ACTIONS.INPUT:
            return {
                ...search,
                [action.payload.name]: action.payload.value,
            }
        case SEARCH_ACTIONS.FETCH:
            return {
                ...search,
                loading: true,
                error: '',
                page: 1,
            }
        case SEARCH_ACTIONS.PAGINATE:
            return {
                ...search,
                page: action.payload.page,
                loading: true,
                error: '',
            }
        case SEARCH_ACTIONS.SUCCESS:
            return {
                ...search,
                loading: false,
                moviesList: action.payload.moviesList,
                count: action.payload.count,
                error: '',
            }
        case SEARCH_ACTIONS.ERROR:
            return {
                ...search,
                loading: false,
                error: action.payload.error,
                moviesList: [],
                count: 0,
            }
        default:
            return search
    }
}

export default searchReducer
