import React, { useContext, useRef, useCallback, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import { Link } from 'react-router-dom'
import MOVIE_ACTIONS from '../actions/movieActions'
import moviesContext from '../context/moviesContext'
import poster from '../img/poster.jpg'
import ScrollToTop from './ScrollToTop'

const GridList = () => {
    const { movies, moviesDispatch, scroll, setScroll } = useContext(moviesContext)
    const { pathname } = useLocation()
    const observer = useRef(null)

    useEffect(() => {
        window.scrollTo(0, scroll)
    }, [scroll, pathname])

    const handleScroll = e => {
        setScroll(window.scrollY)
    }

    const lastMovie = useCallback(
        node => {
            if (!node) return
            if (movies.loading) return
            if (observer.current) observer.current.disconnect()
            observer.current = new IntersectionObserver(entries => {
                if (entries[0].isIntersecting) {
                    moviesDispatch({ type: MOVIE_ACTIONS.PAGINATE })
                }
            })
            observer.current.observe(node)
        },
        [movies.loading, moviesDispatch]
    )

    return (
        <div className='container my-3'>
            <p className='text-muted lead'>
                Total Number of movies:{' '}
                <span className='badge bg-success rounded-pill fw-normal'>{movies.count}</span>
            </p>
            <div className='row row-cols-1 row-cols-md-2 row-cols-lg-4 g-2'>
                {movies.moviesList.length &&
                    movies.moviesList.map((movie, index) => {
                        if (index === movies.moviesList.length - 1) {
                            return (
                                <div ref={lastMovie} key={movie._id} className='col'>
                                    <Link
                                        to={`/movies/${movie._id}`}
                                        className='text-decoration-none text-dark'
                                    >
                                        <div className='card h-100'>
                                            <img
                                                className='card-img-top'
                                                src={movie.poster ? movie.poster : poster}
                                                alt=''
                                                style={{
                                                    height: 200,
                                                    objectFit: 'cover',
                                                    objectPosition: 'top',
                                                }}
                                            />
                                            <div className='card-body'>
                                                <h6 className='card-title'>{movie.title}</h6>
                                                <p className='card-text p-0 m-0 small'>
                                                    Rating: {movie.imdb.rating}
                                                </p>
                                                <p className='card-text p-0 m-0 small'>
                                                    Year: {movie.year}
                                                </p>
                                                <p className='card-text small'>
                                                    Cast: {movie.cast ? movie.cast.join(', ') : ''}
                                                </p>
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                            )
                        } else {
                            return (
                                <div key={movie._id} className='col'>
                                    <Link
                                        to={`/movies/${movie._id}`}
                                        className='text-decoration-none text-dark'
                                        onClick={handleScroll}
                                    >
                                        <div className='card h-100'>
                                            <img
                                                className='card-img-top'
                                                src={movie.poster ? movie.poster : poster}
                                                alt=''
                                                style={{
                                                    height: 200,
                                                    objectFit: 'cover',
                                                    objectPosition: 'top',
                                                }}
                                            />
                                            <div className='card-body'>
                                                <h6 className='card-title'>{movie.title}</h6>
                                                <p className='card-text p-0 m-0 small'>
                                                    Rating: {movie.imdb.rating}
                                                </p>
                                                <p className='card-text p-0 m-0 small'>
                                                    Year: {movie.year}
                                                </p>
                                                <p className='card-text small'>
                                                    Cast: {movie.cast ? movie.cast.join(', ') : ''}
                                                </p>
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                            )
                        }
                    })}
            </div>
            <ScrollToTop />
        </div>
    )
}

export default GridList
