import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import searchContext from '../context/searchContext'
import fetchData from '../services/fetchData'
import SEARCH_ACTIONS from '../actions/searchActions'
import text_search_icon from '../img/text_search_icon.png'
import Pagination from '@material-ui/lab/Pagination'

const Search = () => {
    const { search, searchDispatch } = useContext(searchContext)

    const handleChange = e => {
        searchDispatch({
            type: SEARCH_ACTIONS.INPUT,
            payload: { name: e.target.name, value: e.target.value },
        })
    }

    const handleSubmit = e => {
        e.preventDefault()
        searchDispatch({ type: SEARCH_ACTIONS.FETCH })
        fetchData(search)
            .then(data => {
                searchDispatch({
                    type: SEARCH_ACTIONS.SUCCESS,
                    payload: { moviesList: data.moviesList, count: data.count },
                })
            })
            .catch(error =>
                searchDispatch({ type: SEARCH_ACTIONS.ERROR, payload: { error: error.message } })
            )
    }

    const handlePageChange = (event, page) => {
        searchDispatch({ type: SEARCH_ACTIONS.PAGINATE, payload: { page } })
        fetchData({ ...search, page })
            .then(data => {
                searchDispatch({
                    type: SEARCH_ACTIONS.SUCCESS,
                    payload: { moviesList: data.moviesList, count: data.count },
                })
            })
            .catch(error =>
                searchDispatch({ type: SEARCH_ACTIONS.ERROR, payload: { error: error.message } })
            )
    }

    return (
        <div className='container my-4'>
            <div className='row bg-light border border-grey rounded-3 p-5 mb-4 shadow-lg'>
                <div className='col-3'>
                    <img className='img-fluid' src={text_search_icon} alt='text search icon' />
                </div>
                <div className='col-9'>
                    <h1 className='text-center mb-4'>Let's Watch a Movie!</h1>
                    <form onSubmit={handleSubmit}>
                        <div className='row g-0 mb-2 align-items-center border border-grey rounded-pill px-2 py-0 input-container'>
                            <div className='col-11'>
                                <input
                                    className='form-control border-0 bg-transparent'
                                    id='search-textbox'
                                    type='text'
                                    name='term'
                                    value={search.term}
                                    onChange={handleChange}
                                    autoComplete='off'
                                    placeholder='search movies...'
                                    aria-label='search'
                                />
                            </div>
                            <div className='col-1 text-end'>
                                <span className='material-icons fs-2 align-middle text-success'>
                                    search
                                </span>
                            </div>
                        </div>
                        <div className='row g-0'>
                            <div className='col me-2'>
                                <select
                                    className='form-select rounded-pill bg-transparent'
                                    name='type'
                                    value={search.type}
                                    onChange={handleChange}
                                    aria-label='Default select example'
                                >
                                    <option>Search By</option>
                                    <option value='cast'>Cast</option>
                                    <option value='country'>Country</option>
                                    <option value='genre'>Genre</option>
                                    <option value='text'>Text Search</option>
                                </select>
                            </div>
                            <div className='col'>
                                <button
                                    className='btn btn-outline-success px-4 py-1 rounded-pill w-100'
                                    type='submit'
                                >
                                    {search.loading ? (
                                        <div
                                            className='spinner-border spinner-border-sm border-2'
                                            role='status'
                                        ></div>
                                    ) : (
                                        'Search'
                                    )}
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            <div className='row flex-column p-5'>
                {search.error ? (
                    <div className='alert alert-danger' role='alert'>
                        {search.error}
                    </div>
                ) : (
                    <>
                        <div className='col pb-4'>
                            <Pagination
                                page={search.page}
                                count={Math.ceil(search.count / 20)}
                                onChange={(event, page) => handlePageChange(event, page)}
                                variant='outlined'
                                shape='rounded'
                            />
                        </div>
                        <div className='col'>
                            <ul className='list-group list-group-flush'>
                                {search.moviesList.length ? (
                                    search.moviesList.map(movie => (
                                        <Link
                                            key={movie._id}
                                            to={`/movies/${movie._id}`}
                                            className='list-group-item list-group-item-action'
                                        >
                                            {movie.title}
                                        </Link>
                                    ))
                                ) : (
                                    <p className='ps-2 pt-3 text-muted'>No results found</p>
                                )}
                            </ul>
                        </div>
                    </>
                )}
            </div>
        </div>
    )
}

export default Search
