import MOVIE_ACTIONS from '../actions/movieActions'

const moviesReducer = (state, action) => {
    switch (action.type) {
        case MOVIE_ACTIONS.FETCH:
            return {
                ...state,
                loading: true,
                error: '',
            }
        case MOVIE_ACTIONS.PAGINATE:
            return {
                ...state,
                page: state.page + 1,
            }
        case MOVIE_ACTIONS.SUCCESS:
            return {
                ...state,
                moviesList: [...state.moviesList, ...action.payload.moviesList],
                count: action.payload.count,
                loading: false,
                error: '',
            }
        case MOVIE_ACTIONS.ERROR:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
            }
        default:
            return state
    }
}

export default moviesReducer
