const facetFetch = async state => {
    try {
        if (state.loading) return
        if (state.term === '') return
        const response = await fetch(
            `/api/v1/movies/facet?${state.type}=${state.term}&page=${state.page}`
        )
        return await response.json()
    } catch (e) {
        console.log(e)
        throw new Error(e)
    }
}

export default facetFetch
