export const loadState = async () => {
    try {
        const user = localStorage.getItem('user')
        if (user === null) return undefined
        const userObj = JSON.parse(user)
        const response = await fetch('/api/v1/users/validate', {
            method: 'POST',
            headers: {
                Authorization: `Bearer ${userObj.auth_token}`,
            },
        })

        const result = await response.json()
        if (result.error) {
            localStorage.setItem(
                'user',
                JSON.stringify({
                    auth_token: '',
                    info: {
                        name: '',
                        email: '',
                        preferences: {
                            favorite_cast: '',
                            preferred_language: '',
                        },
                    },
                    loggedIn: false,
                    isAdmin: false,
                    error: '',
                })
            )
            return undefined
        } else {
            return result
        }
    } catch (error) {
        return undefined
    }
}

export const saveState = user => {
    try {
        const serializedUser = JSON.stringify(user)
        localStorage.setItem('user', serializedUser)
    } catch (error) {
        console.log(error)
    }
}
