import React, { useState, useContext } from 'react'
import { Link } from 'react-router-dom'
import facetContext from '../context/facetContext'
import facetFetch from '../services/facetFetch'
import FACET_ACTIONS from '../actions/facetActions'
import text_search_icon from '../img/text_search_icon.png'
import Pagination from '@material-ui/lab/Pagination'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'

const Facet = () => {
    const [rating, setRating] = useState('all')
    const handleRatingChange = event => {
        const { value } = event.target
        switch (value) {
            case 'all':
                setRating('all')
                break
            case 'other':
                setRating('other')
                break
            default:
                setRating(Number(value))
                break
        }
    }

    const { facet, facetDispatch } = useContext(facetContext)

    const handleChange = e => {
        facetDispatch({
            type: FACET_ACTIONS.INPUT,
            payload: { name: e.target.name, value: e.target.value },
        })
    }

    const handleSubmit = e => {
        e.preventDefault()
        facetDispatch({ type: FACET_ACTIONS.FETCH })
        facetFetch(facet)
            .then(data => {
                facetDispatch({
                    type: FACET_ACTIONS.SUCCESS,
                    payload: {
                        moviesList: data.moviesList,
                        rating: data.rating,
                        runtime: data.runtime,
                        count: data.count,
                    },
                })
            })
            .catch(error =>
                facetDispatch({ type: FACET_ACTIONS.ERROR, payload: { error: error.message } })
            )
    }

    const handlePageChange = (event, page) => {
        facetDispatch({ type: FACET_ACTIONS.PAGINATE, payload: { page } })
        facetFetch({ ...facet, page })
            .then(data => {
                facetDispatch({
                    type: FACET_ACTIONS.SUCCESS,
                    payload: {
                        moviesList: data.moviesList,
                        rating: data.rating,
                        runtime: data.runtime,
                        count: data.count,
                    },
                })
            })
            .catch(error =>
                facetDispatch({ type: FACET_ACTIONS.ERROR, payload: { error: error.message } })
            )
    }

    return (
        <div className='container my-4'>
            <div className='row bg-light border border-grey rounded-3 p-5 mb-4 shadow-lg'>
                <div className='col-3'>
                    <img className='img-fluid' src={text_search_icon} alt='text search icon' />
                </div>
                <div className='col-9'>
                    <h1 className='text-center mb-4'>Faceted Search</h1>
                    <form onSubmit={handleSubmit}>
                        <div className='row g-0 mb-2 align-items-center border border-grey rounded-pill px-2 py-0 input-container'>
                            <div className='col-11'>
                                <input
                                    className='form-control border-0 bg-transparent'
                                    id='search-textbox'
                                    type='text'
                                    name='term'
                                    value={facet.term}
                                    onChange={handleChange}
                                    autoComplete='off'
                                    placeholder='search movies...'
                                    aria-label='search'
                                />
                            </div>
                            <div className='col-1 text-end'>
                                <span className='material-icons fs-2 align-middle text-success'>
                                    search
                                </span>
                            </div>
                        </div>
                        <div className='row g-0'>
                            <div className='col me-2'>
                                <select
                                    className='form-select rounded-pill bg-transparent'
                                    name='type'
                                    value={facet.type}
                                    onChange={handleChange}
                                    aria-label='Default select example'
                                >
                                    <option>Search By</option>
                                    <option value='cast'>Cast</option>
                                    <option value='country'>Country</option>
                                    <option value='genre'>Genre</option>
                                </select>
                            </div>
                            <div className='col'>
                                <button
                                    className='btn btn-outline-success px-4 py-1 rounded-pill w-100'
                                    type='submit'
                                >
                                    {facet.loading ? (
                                        <div
                                            className='spinner-border spinner-border-sm border-2'
                                            role='status'
                                        ></div>
                                    ) : (
                                        'Search'
                                    )}
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            <div className='row flex-column p-5'>
                {facet.error ? (
                    <div className='alert alert-danger' role='alert'>
                        {facet.error}
                    </div>
                ) : (
                    <>
                        <div className='col d-block d-lg-flex justify-content-between align-items-end pb-4'>
                            <div>
                                <p className='px-2 text-muted'>Pages</p>
                                <Pagination
                                    className='pb-4 pb-lg-0'
                                    page={facet.page}
                                    count={Math.ceil(facet.count / 20)}
                                    onChange={(event, page) => handlePageChange(event, page)}
                                    variant='outlined'
                                    shape='rounded'
                                />
                            </div>
                            {facet.rating.length ? (
                                <FormControl className='ps-1 ps-lg-0' component='fieldset'>
                                    <FormLabel className='ps-1' component='legend'>
                                        Rating
                                    </FormLabel>
                                    <RadioGroup
                                        aria-label='rating'
                                        row
                                        name='rating'
                                        value={rating}
                                        onChange={handleRatingChange}
                                    >
                                        {facet.rating.map(bucket => (
                                            <FormControlLabel
                                                key={bucket._id}
                                                value={bucket._id}
                                                control={<Radio />}
                                                label={
                                                    bucket._id === 'other'
                                                        ? `other (${bucket.count})`
                                                        : `${bucket._id}-${bucket._id + 20} (${
                                                              bucket.count
                                                          })`
                                                }
                                            />
                                        ))}
                                        <FormControlLabel
                                            value='all'
                                            control={<Radio />}
                                            label={`All (${facet.count < 20 ? facet.count : '20'})`}
                                        />
                                    </RadioGroup>
                                </FormControl>
                            ) : null}
                        </div>
                        <div className='col'>
                            <ul className='list-group list-group-flush'>
                                {facet.moviesList.length ? (
                                    facet.moviesList.map(movie => {
                                        if (rating === 'all') {
                                            return (
                                                <Link
                                                    key={movie._id}
                                                    to={`/movies/${movie._id}`}
                                                    className='list-group-item list-group-item-action'
                                                >
                                                    {movie.title}
                                                </Link>
                                            )
                                        }

                                        if (rating === 'other' && !movie.metacritic) {
                                            return (
                                                <Link
                                                    key={movie._id}
                                                    to={`/movies/${movie._id}`}
                                                    className='list-group-item list-group-item-action'
                                                >
                                                    {movie.title}
                                                </Link>
                                            )
                                        }

                                        if (
                                            movie.metacritic >= rating &&
                                            movie.metacritic < rating + 20
                                        ) {
                                            return (
                                                <Link
                                                    key={movie._id}
                                                    to={`/movies/${movie._id}`}
                                                    className='list-group-item list-group-item-action'
                                                >
                                                    {movie.title}
                                                </Link>
                                            )
                                        } else {
                                            return null
                                        }
                                    })
                                ) : (
                                    <p className='ps-2 pt-3 text-muted'>No results found</p>
                                )}
                            </ul>
                        </div>
                    </>
                )}
            </div>
        </div>
    )
}

export default Facet
