import React, { useRef, useContext } from 'react'
import { useHistory } from 'react-router-dom'
import USER_ACTIONS from '../actions/userActions'
import NotificationContext from '../context/NotificationContext'
import userContext from '../context/userContext'

const Dashboard = () => {
    const { user, userDispatch } = useContext(userContext)
    const { showNotification } = useContext(NotificationContext)
    const castInputRef = useRef(null)
    const langInputRef = useRef(null)
    const passwordInputRef = useRef(null)
    const history = useHistory()

    const handleUpdate = async e => {
        try {
            e.preventDefault()
            const response = await fetch('/api/v1/users/update', {
                method: 'PUT',
                headers: {
                    Authorization: `Bearer ${user.auth_token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    preferences: {
                        favorite_cast: castInputRef.current.value,
                        preferred_language: langInputRef.current.value,
                    },
                }),
            })
            const payload = await response.json()
            if (response.ok) {
                userDispatch({ type: USER_ACTIONS.UPDATE_SUCCESS, payload })
                showNotification({ message: 'Account updated successfully', status: 'success' })
            } else {
                throw new Error(payload.error)
            }
        } catch (error) {
            userDispatch({ type: USER_ACTIONS.UPDATE_FAILED, payload: { error: error.message } })
            showNotification({ message: error.message, status: 'error' })
        }
    }

    const handleDelete = async e => {
        try {
            e.preventDefault()

            const response = await fetch('/api/v1/users/delete', {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${user.auth_token}`,
                },
                body: JSON.stringify({ password: passwordInputRef.current.value }),
            })

            const { error } = await response.json()
            if (error) throw new Error(error)

            userDispatch({ type: USER_ACTIONS.DELETE_SUCCESS })
            showNotification({ message: 'Account Deleted successfully', status: 'success' })
            history.replace('/')
        } catch (error) {
            userDispatch({ type: USER_ACTIONS.DELETE_FAILED, payload: { error: error.message } })
            showNotification({ message: error.message, status: 'error' })
        }
    }

    return (
        <div className='container my-4'>
            <div className='row'>
                {!user.loggedIn ? (
                    <div className='col-md-6 mx-auto'>
                        <span className='lead text-danger'>You need to be logged in!</span>
                    </div>
                ) : (
                    <div className='col-md-6 mx-auto'>
                        <ul className='nav nav-tabs' id='myTab' role='tablist'>
                            <li className='nav-item' role='presentation'>
                                <button
                                    className='nav-link active'
                                    id='home-tab'
                                    data-bs-toggle='tab'
                                    data-bs-target='#home'
                                    type='button'
                                    role='tab'
                                    aria-controls='home'
                                    aria-selected='true'
                                >
                                    Account
                                </button>
                            </li>
                            <li className='nav-item' role='presentation'>
                                <button
                                    className='nav-link'
                                    id='profile-tab'
                                    data-bs-toggle='tab'
                                    data-bs-target='#profile'
                                    type='button'
                                    role='tab'
                                    aria-controls='profile'
                                    aria-selected='false'
                                >
                                    Update
                                </button>
                            </li>
                            <li className='nav-item' role='presentation'>
                                <button
                                    className='nav-link'
                                    id='contact-tab'
                                    data-bs-toggle='tab'
                                    data-bs-target='#contact'
                                    type='button'
                                    role='tab'
                                    aria-controls='contact'
                                    aria-selected='false'
                                >
                                    Delete
                                </button>
                            </li>
                        </ul>
                        <div className='tab-content' id='myTabContent'>
                            <div
                                className='tab-pane fade show active'
                                id='home'
                                role='tabpanel'
                                aria-labelledby='home-tab'
                            >
                                <div className='p-4'>
                                    <ul className='list-group list-group-flush'>
                                        <li className='list-group-item d-flex justify-content-between align-items-center'>
                                            Name:
                                            <span>{user.info.name}</span>
                                        </li>
                                        <li className='list-group-item d-flex justify-content-between align-items-center'>
                                            Email:
                                            <span>{user.info.email}</span>
                                        </li>
                                        <li className='list-group-item d-flex justify-content-between align-items-center'>
                                            Favorite Cast:
                                            <span>{user.info.preferences.favorite_cast}</span>
                                        </li>
                                        <li className='list-group-item d-flex justify-content-between align-items-center'>
                                            Preferred Language:
                                            <span>{user.info.preferences.preferred_language}</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div
                                className='tab-pane fade'
                                id='profile'
                                role='tabpanel'
                                aria-labelledby='profile-tab'
                            >
                                <div className='p-4'>
                                    <form onSubmit={handleUpdate}>
                                        <div className='mb-3'>
                                            <input
                                                type='text'
                                                className='form-control'
                                                id='cast'
                                                ref={castInputRef}
                                                placeholder='Favorite Cast'
                                            />
                                        </div>
                                        <div className='mb-3'>
                                            <input
                                                type='text'
                                                className='form-control'
                                                id='lang'
                                                ref={langInputRef}
                                                placeholder='Preferred Language'
                                            />
                                        </div>
                                        <button
                                            type='submit'
                                            className='btn btn-outline-success w-100'
                                        >
                                            Update
                                        </button>
                                    </form>
                                </div>
                            </div>
                            <div
                                className='tab-pane fade'
                                id='contact'
                                role='tabpanel'
                                aria-labelledby='contact-tab'
                            >
                                <div className='p-4'>
                                    <form onSubmit={handleDelete}>
                                        <div className='mb-3'>
                                            <label
                                                htmlFor='password'
                                                className='form-label mb-4 fs-5'
                                            >
                                                Type your password 👇
                                            </label>
                                            <input
                                                type='password'
                                                className='form-control'
                                                id='password'
                                                ref={passwordInputRef}
                                                placeholder='Password'
                                            />
                                        </div>
                                        <button
                                            type='submit'
                                            className='btn btn-outline-danger w-100'
                                        >
                                            <strong>Delete</strong> My Account 😢
                                        </button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}

export default Dashboard
