import React, { useState, useEffect, useContext } from 'react'
import { Link, useHistory } from 'react-router-dom'
import ReviewForm from './ReviewForm'
import poster from '../img/poster.jpg'
import userContext from '../context/userContext'
import NotificationContext from '../context/NotificationContext'

const ShowPage = ({ match }) => {
    const { user } = useContext(userContext)
    const { showNotification } = useContext(NotificationContext)
    const [isEditing, setIsEditing] = useState(false)
    const history = useHistory()
    const { id } = match.params
    const [movie, setMovie] = useState({
        poster: '',
        title: '',
        imdb: {},
        year: 1900,
        cast: [],
        fullplot: '',
    })
    useEffect(() => {
        async function getMovie() {
            const response = await fetch(`/api/v1/movies/${id}`)
            const result = await response.json()
            setMovie(result)
        }
        getMovie()
    }, [id])

    const addComment = async text => {
        try {
            const response = await fetch(`/api/v1/movies/${id}/comments`, {
                method: 'POST',
                headers: {
                    Authorization: `Bearer ${user.auth_token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ text }),
            })
            const { movie, error } = await response.json()
            if (error) throw new Error(error)
            setMovie(movie)
            showNotification({ message: 'Your review was added successfully.', status: 'success' })
        } catch (error) {
            showNotification({ message: 'Failed to add your comment!', status: 'error' })
        }
    }

    const switchEditMode = () => {
        setIsEditing(prev => !prev)
    }

    const updateComment = async (text, commentId) => {
        try {
            const response = await fetch(`/api/v1/movies/${id}/comments/${commentId}`, {
                method: 'PATCH',
                headers: {
                    Authorization: `Bearer ${user.auth_token}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ text }),
            })
            const { movie, error } = await response.json()
            if (error) throw new Error(error)
            setMovie(movie)
            switchEditMode()
            showNotification({
                message: 'Your review was updated successfully.',
                status: 'success',
            })
        } catch (error) {
            switchEditMode()
            showNotification({ message: 'Failed to update your comment!', status: 'error' })
        }
    }

    const deleteComment = async e => {
        const { commentId } = e.target.dataset
        try {
            const response = await fetch(`/api/v1/movies/${id}/comments/${commentId}`, {
                method: 'DELETE',
                headers: {
                    Authorization: `Bearer ${user.auth_token}`,
                    'Content-Type': 'application/json',
                },
            })
            const { movie, error } = await response.json()
            if (error) throw new Error(error)
            setMovie(movie)
            showNotification({
                message: 'Your review was deleted successfully.',
                status: 'success',
            })
        } catch (error) {
            showNotification({ message: 'Failed to delete your comment!', status: 'error' })
        }
    }

    return movie.error ? (
        <div className='container my-3'>
            <div className='row'>
                <div className='col col-xl-6 mx-auto'>
                    <div className='alert alert-warning alert-dismissible fade show' role='alert'>
                        <strong>Holy guacamole!</strong> {movie.error}
                        <div className='mt-1'>
                            <Link to='/movies' className='link'>
                                Back to Movies
                            </Link>
                        </div>
                        <button
                            type='button'
                            className='btn-close'
                            data-bs-dismiss='alert'
                            aria-label='Close'
                        ></button>
                    </div>
                </div>
            </div>
        </div>
    ) : (
        <>
            <div className='container py-5'>
                <div className=''>
                    <div className='row g-4'>
                        <div className='col-md-4'>
                            <img
                                className='img-fluid'
                                src={movie.poster ? movie.poster : poster}
                                alt=''
                            />
                        </div>
                        <div className='col-md-8'>
                            <div className='card-body'>
                                <h5 className='card-title mb-3'>{movie.title}</h5>
                                <p className='card-text mb-0'>Rating: {movie.metacritic}</p>
                                <p className='card-text mb-0'>Year: {movie.year}</p>
                                <p className='card-text'>
                                    Cast: {movie.cast ? movie.cast.join(', ') : ''}
                                </p>
                                <p className='card-text'>{movie.fullplot}</p>
                                <p className='card-text'>
                                    <small className='text-muted'>Last updated 3 mins ago</small>
                                </p>
                                <button
                                    className='btn btn-outline-secondary px-3 py-1 me-3'
                                    onClick={() => history.goBack()}
                                >
                                    Go back
                                </button>
                                <Link to='/movies' className='card-link'>
                                    Back to Movies
                                </Link>
                                <Link to='/' className='card-link'>
                                    Home
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {user.loggedIn && (
                <div className='bg-dark'>
                    <div className='container py-5'>
                        <p className='h1 text-muted mb-4'>Leave a Review</p>
                        <ReviewForm initialValue='' submitHandler={addComment} mode='new' />
                    </div>
                </div>
            )}
            <div className='bg-light'>
                <div className='container py-5'>
                    <p className='h2 text-muted mb-4'>Past Reviews</p>
                    <div className=''>
                        <div className='row row-cols-1 row-cols-sm-2 row-cols-md-3 g-4'>
                            {movie.comments
                                ? movie.comments.map(comment => {
                                      if (user.info.email === comment.email) {
                                          if (isEditing) {
                                              return (
                                                  <ReviewForm
                                                      initialValue={comment.text}
                                                      submitHandler={updateComment}
                                                      switchEditMode={switchEditMode}
                                                      commentId={comment._id}
                                                      mode='update'
                                                  />
                                              )
                                          } else {
                                              return (
                                                  <div className='col' key={comment._id}>
                                                      <div className='card h-100'>
                                                          <div className='card-body d-flex flex-column'>
                                                              <div className='card-title lead'>
                                                                  {comment.name}
                                                              </div>
                                                              <p className='card-subtitle text-muted small mb-2 fw-bold'>
                                                                  {new Date(
                                                                      comment.date
                                                                  ).toLocaleString()}
                                                              </p>
                                                              <p className='card-text'>
                                                                  {comment.text}
                                                              </p>
                                                              <div className='d-flex justify-content-between mt-auto'>
                                                                  <button
                                                                      className='btn btn-link link-success m-0 p-0'
                                                                      onClick={switchEditMode}
                                                                  >
                                                                      Update
                                                                  </button>
                                                                  <button
                                                                      className='btn btn-link link-danger m-0 p-0'
                                                                      data-comment-id={comment._id}
                                                                      onClick={deleteComment}
                                                                  >
                                                                      Delete
                                                                  </button>
                                                              </div>
                                                          </div>
                                                      </div>
                                                  </div>
                                              )
                                          }
                                      }
                                      return (
                                          <div className='col' key={comment._id}>
                                              <div className='card h-100'>
                                                  <div className='card-body d-flex flex-column'>
                                                      <div className='card-title lead'>
                                                          {comment.name}
                                                      </div>
                                                      <p className='card-subtitle text-muted small mb-2 fw-bold'>
                                                          {new Date(comment.date).toLocaleString()}
                                                      </p>
                                                      <p className='card-text'>{comment.text}</p>
                                                  </div>
                                              </div>
                                          </div>
                                      )
                                  })
                                : null}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ShowPage
