import FACET_ACTIONS from '../actions/facetActions'

const facetReducer = (state, action) => {
    switch (action.type) {
        case FACET_ACTIONS.INPUT:
            return {
                ...state,
                [action.payload.name]: action.payload.value,
            }
        case FACET_ACTIONS.FETCH:
            return {
                ...state,
                loading: true,
                error: '',
                page: 1,
            }
        case FACET_ACTIONS.PAGINATE:
            return {
                ...state,
                page: action.payload.page,
                loading: true,
                error: '',
            }
        case FACET_ACTIONS.SUCCESS:
            return {
                ...state,
                loading: false,
                error: '',
                moviesList: action.payload.moviesList,
                rating: action.payload.rating,
                runtime: action.payload.runtime,
                count: action.payload.count,
            }
        case FACET_ACTIONS.ERROR:
            return {
                ...state,
                loading: false,
                error: action.payload.error,
                moviesList: [],
                rating: [],
                runtime: [],
                count: 0,
            }
        default:
            return state
    }
}

export default facetReducer
