import React from 'react'

const userContext = React.createContext({
    user: {
        auth_token: '',
        info: {
            name: '',
            email: '',
            preferences: {
                favorite_cast: '',
                preferred_language: '',
            },
        },
        loggedIn: false,
        isAdmin: false,
        error: '',
    },
    userDispatch: () => {},
})

export default userContext
