import React, { useState, useContext } from 'react'
import { Link } from 'react-router-dom'
import userContext from '../context/userContext'
import USER_ACTIONS from '../actions/userActions'

const SignupCard = () => {
    const { user, userDispatch } = useContext(userContext)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const handleSubmit = async event => {
        event.preventDefault()
        fetch('/api/v1/users/register', {
            method: 'POST',
            headers: {
                'content-type': 'application/json',
            },
            body: JSON.stringify({ name, email, password }),
        })
            .then(res => res.json())
            .then(result => {
                if (result.error) {
                    userDispatch({
                        type: USER_ACTIONS.LOGIN_FAILED,
                        payload: { error: result.error },
                    })
                } else {
                    userDispatch({
                        type: USER_ACTIONS.LOGIN_SUCCESS,
                        payload: { auth_token: result.auth_token, info: result.info },
                    })
                }
            })
            .catch(error => {
                console.log({ error })
                userDispatch({ type: USER_ACTIONS.LOGIN_FAILED, payload: { error: error.message } })
            })
        setName('')
        setEmail('')
        setPassword('')
    }
    return (
        <div className='container my-4'>
            <div className='row'>
                <div className='col-md-6 mx-auto'>
                    <div className='card border-0 bg-dark py-4 px-3 rounded-3'>
                        <div className='card-body'>
                            {user.error ? (
                                <>
                                    <h5 className='card-title text-light text-center mb-4'>
                                        Sign Up
                                    </h5>
                                    <div
                                        className='alert bg-transparent border border-danger text-danger'
                                        role='alert'
                                    >
                                        {user.error}
                                    </div>
                                </>
                            ) : (
                                ''
                            )}
                            {user.loggedIn ? (
                                <div className='text-light'>
                                    Welcome {user.info.name}, You are logged in!
                                </div>
                            ) : (
                                <>
                                    {user.error ? (
                                        ''
                                    ) : (
                                        <h5 className='card-title text-light text-center mb-4'>
                                            Sign Up
                                        </h5>
                                    )}
                                    <form onSubmit={handleSubmit}>
                                        <input
                                            type='text'
                                            className='form-control rounded rounded-pill mb-3 ps-3'
                                            id='floatingName'
                                            placeholder='name'
                                            value={name}
                                            onChange={e => setName(e.target.value)}
                                        />
                                        <input
                                            type='email'
                                            className='form-control rounded rounded-pill mb-3 ps-3'
                                            id='floatingEmail'
                                            placeholder='name@example.com'
                                            value={email}
                                            onChange={e => setEmail(e.target.value)}
                                        />
                                        <input
                                            type='password'
                                            className='form-control rounded rounded-pill mb-3 ps-3'
                                            id='floatingPassword'
                                            placeholder='Password'
                                            value={password}
                                            onChange={e => setPassword(e.target.value)}
                                        />
                                        <div className='d-flex justify-content-center align-items-center'>
                                            <input
                                                type='submit'
                                                className='btn btn-outline-success rounded rounded-pill flex-grow-1 me-2'
                                                value='Sign Up'
                                            />
                                            <Link
                                                to='/'
                                                className='btn btn-outline-danger rounded rounded-pill flex-grow-1'
                                            >
                                                Cancel
                                            </Link>
                                        </div>
                                    </form>
                                    <div className='d-flex mt-4'>
                                        <p className='card-text text-muted m-0'>
                                            Already have an account?
                                        </p>
                                        <Link to='/login' className='m-0 ms-2'>
                                            Log In
                                        </Link>
                                    </div>
                                </>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SignupCard
