import USER_ACTIONS from '../actions/userActions'

const userReducer = (user, action) => {
    switch (action.type) {
        case USER_ACTIONS.LOGIN_SUCCESS:
            return {
                auth_token: action.payload.auth_token,
                info: {
                    ...user.info,
                    ...action.payload.info,
                },
                loggedIn: true,
                isAdmin: false,
                error: '',
            }
        case USER_ACTIONS.LOGIN_FAILED:
            return {
                auth_token: '',
                info: {
                    name: '',
                    email: '',
                    preferences: {
                        favorite_cast: '',
                        preferred_language: '',
                    },
                },
                loggedIn: false,
                isAdmin: false,
                error: action.payload.error,
            }
        case USER_ACTIONS.LOGOUT_SUCCESS:
            return {
                auth_token: '',
                info: {
                    name: '',
                    email: '',
                    preferences: {
                        favorite_cast: '',
                        preferred_language: '',
                    },
                },
                loggedIn: false,
                isAdmin: false,
                error: '',
            }
        case USER_ACTIONS.LOGOUT_FAILED:
            return {
                ...user,
                error: action.payload.error,
            }
        case USER_ACTIONS.UPDATE_SUCCESS:
            return {
                ...user,
                ...action.payload,
            }
        case USER_ACTIONS.UPDATE_FAILED:
            return {
                ...user,
                error: action.payload.error,
            }
        case USER_ACTIONS.DELETE_SUCCESS:
            return {
                auth_token: '',
                info: {
                    name: '',
                    email: '',
                    preferences: {
                        favorite_cast: '',
                        preferred_language: '',
                    },
                },
                loggedIn: false,
                isAdmin: false,
                error: '',
            }
        case USER_ACTIONS.DELETE_FAILED:
            return {
                ...user,
                error: action.payload.error,
            }
        default:
            return user
    }
}

export default userReducer
