import React, { useMemo, useReducer, useEffect, useState } from 'react'
import Header from './Header'
import Main from './Main'
import Footer from './Footer'
import searchContext from '../context/searchContext'
import moviesContext from '../context/moviesContext'
import facetContext from '../context/facetContext'
import userContext from '../context/userContext'
import searchReducer from '../reducers/searchReducer'
import facetReducer from '../reducers/facetReducer'
import userReducer from '../reducers/userReducer'
import { NotificationContextProvider } from '../context/NotificationContext'
import Notification from './ui/Notification'
import { loadState, saveState } from '../store/localStorage'

import MOVIE_ACTIONS from '../actions/movieActions'
import moviesReducer from '../reducers/moviesReducer'
import USER_ACTIONS from '../actions/userActions'

let initialState = {
    auth_token: '',
    info: {
        name: '',
        email: '',
        preferences: {
            favorite_cast: '',
            preferred_language: '',
        },
    },
    loggedIn: false,
    isAdmin: false,
    error: '',
}

function App() {
    const [user, userDispatch] = useReducer(userReducer, initialState)
    const userProviderValue = useMemo(() => ({ user, userDispatch }), [user, userDispatch])
    useEffect(() => {
        const loadInitialState = async () => {
            const localUser = await loadState()
            if (localUser)
                userDispatch({
                    type: USER_ACTIONS.LOGIN_SUCCESS,
                    payload: {
                        auth_token: localUser.auth_token,
                        info: localUser.info,
                    },
                })
        }
        loadInitialState()
    }, [])
    useEffect(() => {
        saveState(user)
    }, [user])

    const [search, searchDispatch] = useReducer(searchReducer, {
        term: '',
        type: '',
        moviesList: [],
        page: 1,
        count: 0,
        loading: false,
        error: '',
    })
    const searchProviderValue = useMemo(
        () => ({
            search,
            searchDispatch,
        }),
        [search, searchDispatch]
    )

    const [facet, facetDispatch] = useReducer(facetReducer, {
        term: '',
        type: '',
        page: 1,
        moviesList: [],
        rating: [],
        runtime: [],
        count: 0,
        loading: false,
        error: '',
    })
    const facetProviderValue = useMemo(() => ({ facet, facetDispatch }), [facet, facetDispatch])

    const [movies, moviesDispatch] = useReducer(moviesReducer, {
        moviesList: [],
        page: 1,
        count: 0,
        loading: false,
        error: '',
    })
    const [scroll, setScroll] = useState(0)

    const moviesProviderValue = useMemo(
        () => ({ movies, moviesDispatch, scroll, setScroll }),
        [movies, moviesDispatch, scroll, setScroll]
    )

    useEffect(() => {
        moviesDispatch({ type: MOVIE_ACTIONS.FETCH })
        async function fetchData() {
            try {
                const response = await fetch(`/api/v1/movies/search?page=${movies.page}`)
                const data = await response.json()
                moviesDispatch({
                    type: MOVIE_ACTIONS.SUCCESS,
                    payload: { moviesList: data.moviesList, count: data.count },
                })
            } catch (e) {
                moviesDispatch({ type: MOVIE_ACTIONS.ERROR, payload: { error: e.message } })
            }
        }
        fetchData()
    }, [movies.page])

    return (
        <div className='d-flex flex-column vh-100'>
            <NotificationContextProvider>
                <userContext.Provider value={userProviderValue}>
                    <moviesContext.Provider value={moviesProviderValue}>
                        <searchContext.Provider value={searchProviderValue}>
                            <facetContext.Provider value={facetProviderValue}>
                                <Header />
                                <Main />
                                <Notification />
                                <Footer />
                            </facetContext.Provider>
                        </searchContext.Provider>
                    </moviesContext.Provider>
                </userContext.Provider>
            </NotificationContextProvider>
        </div>
    )
}

export default App
